import React, { useEffect, useState } from 'react';
import { cities } from '../utils/api';
import { Link } from 'react-router-dom';

const CitiesScreen = () => {
  const [cityData, setCityData] = useState([]);

  useEffect(() => {
    const fetchCities = async () => {
      const data = await cities();
      setCityData(data);
    };

    fetchCities();
  }, []);

  return (
    <>
      <h1 style={{ margin: '25px 16px 16px' }}>Şehirler</h1>
      <div className="cities-container">
        {cityData.map((city) => (
          <Link to={`/sehir/${city.name}`} key={city.id} className="city-card">
            <h2>{city.name}</h2>
          </Link>
        ))}
      </div>
    </>
  );
};

export default CitiesScreen;
